.search_container
{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-top: 8%;
}

.title
{
    padding: 20px;
    font-size: 30px;
    font-family: sans-serif;
    font-weight: 400;
    margin-bottom: 30px;
}

.search
{
    width: 60%;
    min-width: 350px;
}

.search input
{
    width: 100%;
    height: 100%;
    padding: 20px;
    border-radius: 20px;
    border: 2px rgb(133, 133, 133) solid;
    outline: none;
    
    font-size: 20px;
    font-family: 'Times New Roman', Times, serif;
}

.search ::selection
{
    background-color: rgb(216, 216, 216);
}

.submit
{
    width: 15%;
    min-width: 100px;
    padding: 10px;
    margin-top: 10px;
}

.submit button
{
    width: 100%;
    height: 100%;
    padding: 6px;
    margin-top: 10px;
    border-radius: 10px;
    outline: none;
    border: none;
    
    font-size: 24px;
    font-family: 'Source Sans Pro', sans-serif;

    cursor: pointer;
    background-color: rgb(4, 170, 109);
    color: white;
    transition: 0.5s;
}

.submit button:hover
{
    background-color: rgb(2, 136, 87);
}