.single_container
{
    display: flex;
    flex-direction: column;
    margin: 70px;
    margin-left: 50px;
}

.variable
{
    display: flex;
    justify-content: center;
    margin: 10px;
    margin-bottom: 15px;
}

.variable span
{
    font-size: 30px;
   /* border-left: 6px solid orangered;*/
    border-bottom: 3px solid red;
    margin: 5px;
    padding: 5px;
    text-align: justify;
    font-family: 'Times New Roman', Times, serif;
}

.single_title
{
    border-left : 5px solid orangered;
    margin: 5px;
    padding: 10px;
    font-size: xx-large;
    font-family: 'Times New Roman', Times, serif;
}

.definition
{
    padding-left: 20px;
}

.definition p
{
    text-align: justify;
    font-size: 18px;
}

.link
{
    padding: 10px;
    padding-left: 20px;
    margin-bottom: 15px;
    overflow:hidden;
    overflow-x: auto;
}

.link a
{
    cursor: pointer;
    color: rgb(0, 95, 82);
    font-size: 18px;
    transition: 0.5s;

}

.link a:hover
{
    color: rgb(0, 219, 190);
}

.operation
{
    display: flex;
}

.pdf
{
    display: flex;
    justify-content: top;
}

.pdf figure
{
    width: auto;
    height: auto;
    text-align: center;
    cursor: pointer;
    padding: 8px;

    transition: 0.6s;
}

.pdf figure:hover
{
    background-color: rgb(224, 224, 224);
    border-radius: 15px;
}