.none-container
{
    display: flex;
    justify-content: center;
}

.none-container span
{
    text-align: center;
    font-size: x-large;
    font-family: Georgia, 'Times New Roman', Times, serif;

    margin: 50px;
    padding: 25px;
    border: 2px solid green;
    border-radius: 10px;
    background-color: rgb(203, 255, 203);

    width: 40%;

    min-width: 200px;
}